@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;600;700;800&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* font-family: 'Nunito', sans-serif; */
  font-family: 'Titillium Web', sans-serif;
  line-height: 1.2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

::-webkit-scrollbar{
  width: 6px;
  background: #6f6f6f;
}

::-webkit-scrollbar-thumb{
  background: lightgray;
}

a, a:hover{
  color: white;
  text-decoration: none;
}

/* Common */

.header{
  display: flex;
  justify-content: center;
}

.headerText{
  font-size: 3rem;
  font-weight: 600;
  color: #383838;
  width: min-content;
  text-align: center;
}

.headerBar{
  border: solid 2px #484848;
  margin: 1rem 2rem;
}

.visible{
  height: 12rem !important;
}

/* Navbar */

.navbar, .mobileNavbar{
  position: sticky;
  top: 0;
  z-index: 9;
  height: 4rem;
  width: 100%;
  background: #2c303a;
  display: flex;
  align-items: center;
  padding: 0.5rem 4rem;
  border-bottom: solid 3px #01c4ff;
}

.mobileNavbar{
  padding: 0;
  flex-direction: column;
}

.menuIcon{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  width: 100%;
  padding: 0.5rem;
  cursor: pointer;
}

.menu{
  width: 2.5rem !important;
  height: 2.5rem !important;
}

.menuData{
  height: 0;
  position: absolute;
  top: 4rem;
  width: 100%;
  background: #1f2229;
  overflow: hidden;
  transition: height 0.5s ease-out;
  -o-transition: height 0.5s ease-out;
  -webkit-transition: height 0.5s ease-out;
  -moz-transition: height 0.5s ease-out;
}

.menuData .nav-items{
  font-size: 17px;
  color: white;
  margin: 0;
  padding: 1rem 1rem 0 1rem;
}

.menuData .nav-items:last-child{
  padding-bottom: 1rem;
}

.nav-items{
  font-size: 18px;
  color: white;
  margin-right: 2.5rem;
}

.nav-items a{
  cursor: pointer;
}

.nav-items > .active, .nav-items a:hover{
  color: #00d0ff;
}

.nav-items a:hover{
  transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
}

/* Home section */

.home{
  height: 100vh;
  background-color: #171a23;
  position: relative;
}

.particles{
  position: absolute;
  height: inherit;
  width: 100%;
}

.introduction{
  display: flex;
  height: inherit;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.text{
  z-index: 9;
  font-size: 3.7rem;
  color: white;
  margin-bottom: 1rem;
  text-align: center;
}

.highlight0{
  color: #00c4ff;
  display: inline;
}

.highlight0:first-child{
  margin-left: 0.8rem;
}

.viewBtn{
  display: flex;
  position: relative;
  padding: 0.6rem 1.2rem;
  border: solid 2px white;
  color: white;
  align-items: center;
  font-size: 22px;
  cursor: pointer;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  -ms-transition: all 0.5s;
}

.viewBtn:hover{
  background-color: #04c2c9;
  border: solid 2px transparent;
}

.viewBtn:hover > .arrow{
  transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
}

.arrow{
  height: 1.8rem;
  margin-left: 0.5rem;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  -ms-transition: all 0.5s;
}

/* About */

#about{
  padding: 4rem 3rem;
}

.dataFlex{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4rem 1rem;
}

.dataFlex .hexagon{
  margin: 0 2rem;
}

.boxHolder{
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.dataBox{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 16rem;
  padding: 0 0.5rem;
}

.dataDesc, .portfolio{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.dataHeaders{
  font-size: 26px;
  font-weight: 700;
  color: #4e4e4e;
  margin-top: 1rem;
  margin-bottom: 0.3rem;
}

.dataPara{
  font-size: 17px;
  color: #4e4e4ee0;
  line-height: 18pt;
  text-align: center;
}

.descGrid{
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  margin: 5rem 0 0; 
}

.portfolioImg{
  width: 15rem;
  height: 15rem;
  object-fit: cover;
  border-radius: 50%;
}

.portfolioDesc{
  font-size: 18px;
  line-height: 18pt;
  text-align: justify;
  margin: 1rem 0;
  padding: 0 3rem;
  color: #6f6f6f;
}

.hexagon{
  width: 57px;
  height: 100px;
  background: #04c2c9;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hexagon::before, .hexagon::after{
  content: "";
  border: inherit;
  position: absolute;
  top: 0px;
  left: -0.1px;
  background-color: #04c2c9;
  border-radius: inherit;
  height: 100%;
  width: 100%;
  z-index: -999;
}

.hexagon:before {
  transform: rotate(60deg);
  -ms-transform: rotate(60deg);
  -webkit-transform: rotate(60deg);
  -o-transform: rotate(60deg);
  -moz-transform: rotate(60deg);
}

.hexagon:after {
  transform: rotate(-60deg);
  -ms-transform: rotate(-60deg);
  -webkit-transform: rotate(-60deg);
  -o-transform: rotate(-60deg);
  -moz-transform: rotate(-60deg)
}

.iconify{
  color: white;
  height: 4rem;
  width: 4rem;
  z-index: 999;
}

.logos{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 2.5rem 0;
}

.logos div{
  justify-self: center;
  height: 6rem;
  width: 10rem;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
}

.logos div:hover{
  transform: translateY(-10px) scale(1.1);
  -webkit-transform: translateY(-10px) scale(1.1);
  -ms-transform: translateY(-10px) scale(1.1);
  -o-transform: translateY(-10px) scale(1.1);
  -moz-transform: translateY(-10px) scale(1.1);
}

.progLogo{
  height: 100%;
  width: 100%;
  object-fit: contain;
}

/* Projects */

#projects{
  padding: 4rem 3rem;
  background-color: #efefef;
  position: relative;
}

.flex-box{
  position: relative;
  margin-top: 3.5rem;
}

.flex-bar{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
}

.bar-items{
  padding: 0.3rem 3rem 0.3rem 3rem;
  font-size: 18px;
  color: #3e3e3e;
  font-weight: 700;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.bar-active{
  color: white;
  background:#01c4ff;
}

.gallery{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 2rem 0;
  min-height: 25rem;
}

.card{
  height: 20rem;
  width: 23rem;
  position: relative;
  background: url(./assets/images/project-images/Airbnb1.png);
  animation: fadein 0.6s ease-in;
  -webkit-animation: fadein 0.6s ease-in;
  -moz-animation: fadein 0.6s ease-in;
  -o-animation: fadein 0.6s ease-in;
}

.card:hover > .overlay-card{
  opacity: 1;
}

.overlay-card {
  position: absolute;
  display: flex;
  height: 100%;
  flex-direction: column;
  top: 0;
  left: 0;
  width: 100%;
  background: white;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in;
  -o-transition: all 0.5s ease-in;
  -moz-transition: all 0.5s ease-in;
}

.project-header{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.project-title{
  font-size: 2.5rem;
  font-weight: 600;
  color: #2c303a;
}

.project-stack{
  font-size: 1.3rem;
  margin-top: 0.5rem;
  color: #016a82;
}

.view-btn{
  padding: 0.8rem 2rem;
  margin: 2rem 0;
  border: solid 2px #01c4ff;
  font-size: 22px;
  color: #1f2229;
  text-transform: UPPERCASE;
  cursor: pointer;
  transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
}

.view-btn:hover{
  color: white;
  background-color: #00c4ff;
}

.card-img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.modal{
  background: rgba(0, 0, 0, 0.3);
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  position: absolute;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.carousel{
  position: relative;
  overflow: hidden;
  height: 40rem;
  width: 60rem;
  background: white;
  animation: fadein 0.3s ease-in;
  -webkit-animation: fadein 0.3s ease-in;
  -o-animation: fadein 0.3s ease-in;
  -moz-animation: fadein 0.3s ease-in;
}

.slider-transition{
  transition: all 0.6s ease-in-out;
  -webkit-transition: all 0.6s ease-in-out;
  -o-transition: all 0.6s ease-in-out;
  -moz-transition: all 0.6s ease-in-out;
}

.sliderTray___-vHFQ{
  display: flex;
}

.slideInner___2mfX9{
  height: 28rem;
  width: 100%;
}

.horizontalSlider___281Ls,
.carousel__slider-tray-wrapper,
.sliderTray___-vHFQ{
  height: 100%;
}

.modal-img{
  height: 100%;
  width: 100%;
  object-fit: fill;
}

.btn-group{
  position: absolute;
  top: 0;
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
}

.btn-move{
  border: none;
  border-radius: 50%;
  background: #2c303a;
  outline: none;
  height: 4rem;
  width: 4rem;
  cursor: pointer;
}

.btn-move:hover{
  background: #23262d;
}

.arrow-icon{
  height: 2rem;
  display: flex;
  color: white;
}

.modal-desc {
  position: absolute;
  background: white;
  bottom: 0;
  width: 100%;
  border-top: solid 1px #cecece;
  padding: 1rem 2rem;
}

.modal-project-title{
  font-size: 2.5rem;
  font-weight: 600;
  color: #3e3e3e;
}

.divider{
  margin: 0.5rem 0;
  border-bottom: solid 1px #cecece;
}

.modal-project-desc{
  font-size: 20px;
  letter-spacing: 0.5px;
}

.modal-grp{
  display: flex;
  margin-top: 2.5rem;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.modal-btn{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.6rem 1.5rem;
  border: solid 2px #01c4ff;
  cursor: pointer;
  color: #3e3e3e;
  transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
}

.modal-btn:hover {
  background: #00c4ff;
  color: white;
}

.modal-btn:hover > .url-icon{
  color: white;
}

.url-icon{
  color: #3e3e3e;
  height: 1.5rem;
  width: 1.5rem;
  margin: 0 0.5rem;
  transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
}

.close-icon{
  color: #3e3e3e;
  height: 2rem;
  width: 2rem;
  cursor: pointer;
}

@keyframes fadein {
  0% {
    opacity: 0;
    transform: scale(0.9);
    -ms-transform: scale(0.9);
    -moz-transform: scale(0.9);
    -webkit-transform: scale(0.9);
    -o-transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
    -ms-transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
    -o-transform: scale(1);
  }
}

/* Experience */

#experience{
  padding: 4rem 0 0 0;
}

.exp-flex{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5rem 0;
  gap: 10rem;
}

.exp-img{
  height: 15rem;
  width: 100%;
  object-fit: cover;
  background: #ececec;
  padding: 1em;
  border-radius: 50%;
  opacity: 0.8;
  transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
}

.exp-img:hover{
  opacity: 1;
}

.exp-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 2.5rem;
}

.exp-name{
  font-size: 2rem;
  font-weight: 600;
  color: #3e3e3e;
}

.exp-desc{
  max-width: 32rem;
  font-size: 20px;
  text-align: justify;
  line-height: 1.4;
  color: #3e3e3e;
}

/* Contact */

#contact{
  padding: 10rem 0 4rem 0;
  background: #1f2229;
  position: relative;
}

#contact::before, #contact::after{
  content: '';
  position: absolute;
  width: 50%;
  top: -2.5rem;
  height: 5rem;
  background: white;
}

#contact::before{
  transform: skewY(4deg);
  -webkit-transform: skewY(4deg);
  -ms-transform: skewY(4deg);
  -o-transform: skewY(4deg);
  -moz-transform: skewY(4deg);
}

#contact::after{
  right: 0;
  transform: rotate(180deg) skewY(-4deg);
  -webkit-transform: rotate(180deg) skewY(-4deg);
  -ms-transform: rotate(180deg) skewY(-4deg);
  -o-transform: rotate(180deg) skewY(-4deg);
  -moz-transform: rotate(180deg) skewY(-4deg);
}

#contact .headerText{
  color: white;
}

#contact .headerBar{
  border-color: white;
}

.contact-header{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 3rem 0;
  gap: 2rem;
}

.contact-msg{
  font-size: 20px;
  color: whitesmoke;
}

.contact-link{
  display: flex;
  gap: 1.5rem;
}

.link-icon{
  height: 2rem;
  width: 2rem;
  transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  -moz-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
}

.link-icon:hover{
  transform: translateY(-3px) !important;
  -o-transform: translateY(-3px) !important;
  -moz-transform: translateY(-3px) !important;
  -webkit-transform: translateY(-3px) !important;
  -ms-transform: translateY(-3px) !important;
  color: #00c4ff;
}

/* Footer */

#footer{
  height: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #2c303a;
  position: relative;
}

.footer-name{
  font-size: 14px;
  text-align: center;
  color: #9c9c9c;
  margin-top: 2rem;
}

.footer-desc{
  color: #04c2c9;
}

.footer-copyright{
  font-size: 16px;
  vertical-align: middle;
}

.scroll-btn{
  padding: 0.6rem;
  position: absolute;
  top: -1.8rem;
  background: #04c2c9;
  cursor: pointer;
  transition: all 0.2s ease-in;
}

.scroll-btn:hover{
  background: #03a1a1;
}

.up-icon{
  width: 2rem;
  height: 2rem;
}

/* Responsive */

